  width: 25px;
}
.search:not(:focus-within) {
  --transition-time-input: 0.2s 0s;
}
@media (prefers-reduced-motion: reduce) {
  .search {
    --transition-time-icon: 0s !important;
    --transition-time-input: 0s !important;
  }
}
.search__input {
  background: transparent;
  border: none;
  color: rgb(0, 0, 0);
  font-size: 1rem;
  opacity: 0;
  outline: none;
  padding: 0;
  transition: width 0.3s 0.25s cubic-bezier(0.4, 0, 0.2, 1), padding 0.3s 0.25s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.3s 0.25s linear;
  width: 0;
