  color: var(--color);
  cursor: pointer;
  display: block;
  height: 100%;
  padding: 0;
  position: absolute;
  width: 100%;
  display: flex;
  left: 0;
}
.search__label:hover, .search__submit:hover, .search__label:focus, .search__submit:focus, .search__label:active, .search__submit:active {
  color: #4396da;
}
.search__label {
  transition: color 0.1s, -webkit-transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.1s;
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.1s, -webkit-transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.search__submit {
  background: none;
  border-radius: 50%;
  border: none;
  box-shadow: 0 0 0 4px inset transparent;
  display: none;
  outline: none;
  transition: color 0.1s, box-shadow 0.1s;
